<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>工站编号 : {{ CurrentUserInfo.work_id }}</span>
        <span class="ml20">工站名称 : {{ workStationName }}</span>
        <span class="ml20">工站类型 : {{ CurrentUserInfo.work_type }}</span>
      </div>
      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 650px"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
      >
        <el-form-item prop="count" label="包装数量">
          <el-input
            ref="firstInput"
            v-model.number="form.count"
            @input="inputChange"
          ></el-input>
        </el-form-item>
        <el-form-item prop="pack_id" label="包装编码">
          <el-input v-model="form.pack_id"></el-input>
        </el-form-item>
        <div
          v-for="(item, index) in form.response_datas"
          :key="index + ' onlyItem'"
        >
          <el-form-item
            :prop="'response_datas.' + index + '.finished_product_code'"
            :rules="[]"
            label="产品编号"
          >
            <el-input
              :ref="'finishedProductCode' + index"
              v-model="item.finished_product_code"
              @input="
                nextFocus(
                  'finishedProductCode' + (index + 1),
                  index,
                  item.finished_product_code
                )
              "
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div>
        <el-button @click="resetForm">重 置</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStation",
  computed: {
    ...mapState(["WorkCodeNameList", "CurrentUserInfo"]),
  },
  data() {
    return {
      timeout: "",
      workStationName: "",
      // 产品过站弹出层
      form: {
        count: "",
        pack_id: "",
        response_datas: [],
      },
      formBase: {
        count: "",
        pack_id: "",
        response_datas: [],
      },
      formRules: {
        count: [
          { required: true, message: "请输入数量", trigger: "blur" },
          { type: "number", message: "数量必须为数值", trigger: "blur" },
        ],
        pack_id: [
          { required: true, message: "请输入包装编号", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName"]),

    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (index < this.form.response_datas.length - 1) {
          this.$nextTick(() => {
            this.$refs[nextRef][0].focus();
          });
        } else {
          console.log(5789);
        }
      }, 500);
    },

    // 产品过站 新增
    postLogisticFinishedProductStorage(params) {
      baseManagement.postLogisticFinishedProductStorage(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.resetForm();
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    inputChange() {
      this.form.response_datas = [];
      for (let index = 0; index < this.form.count; index++) {
        this.form.response_datas.push({
          pack_id: "",
          finished_product_code: "",
        });
      }
    },
    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
      this.$nextTick(() => {
        this.$refs.formRef.clearValidate();
      });
    },

    // 确认按钮
    dialogSave() {
      console.log("this.form", this.form);
      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          this.form.response_datas.forEach((item) => {
            item.pack_id = this.form.pack_id;
          });
          console.log("产品过站", this.form);
          this.postLogisticFinishedProductStorage(this.form);
        }
      });
    },
  },
  mounted() {
    this.getLogisticWorkCodeName();
    this.WorkCodeNameList.forEach((CodeNameListItem) => {
      if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
        this.workStationName = CodeNameListItem.work_name;
      }
    });

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

